import common from '@/libs/common.mixin'

export default{
	mixins: [common],
	data () {
		return {
			data_uri: 'broadcasts',
			data: []
		}
	},
	mounted () {
		
	},
	methods: {
		formatDate (time) {
			return time.substring(0,10)
		},
		getInnerHTML (html) {
			return html.replace(/<[^>]+>/g,"")
		}
	}
}



